.headingFirst{
    font-size: 1.5em;
}
.headingFull{
    font-family: 'Pacifico', cursive;
    font-size: 3em;
    text-align: center;
    margin-top: 1.5rem;
    font-weight: 300;
    padding: 0.5rem;
}
.fullform{
    font-size: 1em;
    font-weight: 500;
    line-height: normal;
}
#video{
    width: fit-content;
    margin: auto;
    margin-top: 3rem;
    border: 0.2rem solid;
}
#frame2{
    display: none;
}
#frame3{
    display: none;
}
#frame3:hover{
    cursor: pointer;
}

#content{
    display: flex;
    justify-content: space-evenly;
    margin: 1.5rem;
}
#updates{
    width: 25vw;
    border: 2px solid black;
    padding: 0.5rem;
    padding-top: 1rem;
    height: 100%;
    text-align: center;
    background-color: rgb(61, 61, 61);
    color: white;
    border-radius: 2rem;
}
li{
    margin: 1rem;
    font-family: 'Roboto Mono', monospace;
    font-size: 1rem;
}
li:first-child{
    margin-top: 0.3rem;
}
#pragraph{
    width: 60vw;
    padding: 1rem;
}
#pragraph2{
    width: fit-content;
    padding: 1rem;
}
#individual{
    font-size: 1.3rem;
    margin: 0.2rem;
    padding: 0.5rem;
    font-family: 'Roboto Mono', monospace;
    text-align: justify;
}
#individual2{
    font-size: 0.9rem;
    margin: 0.2rem;
    padding: 0.5rem;
    font-family: 'Roboto Mono', monospace;
}
#green{
    padding: 0.2rem;
    border-radius: 0.2rem;
    background-color: green;
    color: white;
    line-height: 2em;
}
#yellow{
    padding: 0.2rem;
    border-radius: 0.2rem;
    background-color: yellow;
    line-height: 2em;
}
#red{
    padding: 0.2rem;
    border-radius: 0.2rem;
    background-color: red;
    line-height: 2em;
    color: white;
}
#orange{
    padding: 0.2rem;
    border-radius: 0.2rem;
    background-color: orange;
    line-height: 2em;
}
#popOut{
    position: fixed;
    top: 0rem;
    left: 0;
    width:fit-content;
    max-height: 85vh;
    margin: 1rem;
    padding: 1rem 0.5rem;
    border-radius: 2rem;
    color: white;
    background-color: rgb(47, 47, 47);
    z-index: 1;
    overflow-y: auto;
}

#popOut::-webkit-scrollbar {
    display: none;
  }

#inner{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
.ui.button.vertical.animated{
    font-size: 1.5rem !important;
    width: fit-content !important;
    height: fit-content !important;
    padding: 1rem !important;
    margin: 0.5rem !important;
}
.ui.button.vertical.animated .visible.content{
    margin: auto !important;
}
#click{
    text-decoration: underline;
    color: rgb(99, 99, 189);
    cursor: pointer;
}

.howToUse{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
#video{
    margin-right: 0.8rem;
}

@media (max-width: 1100px){
    .headingFull{
        font-size: 2.5em;
    }
    .headingFirst{
        font-size: 1em;
    }

}

@media (max-width: 770px) {
    .headingFull{
        font-size: 2em;
    }
    #frame1{
        display: none;
    }
    #frame2{
        display: block;
    }
    #content{
        flex-direction: column;
        align-items:center;
    }
    #updates{
        width: fit-content;
        margin: 1rem;
    }
    #pragraph{
        width: fit-content;
    }
}
@media (max-width: 610px){
    .headingFull{
        font-size: 1.5em;
    }
}

@media (max-width: 340px){
    #frame1{
        display: none;
    }
    #frame2{
        display: none;
    }
    #frame3{
        display: block;
    }
    #video{
        border: 0px solid;

    }
    .headingFull{
        margin: 0px !important;
        padding: 0px !important;
    }
}
